

import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'


export default {
name:'myFamilyDoctors',
components:{ Toast },
data(){
return {
showToast:false,
toastMsg:'',
managerId:'',
doctorNum:2,
doctor1:{},
doctor2:{}
}
},
mounted(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.managerId = id;

this.$http.get('/api/contracte/queryDoctors?managerId='+id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
for(let i=0;i<data.data.length;i++){
if(data.data[i].buildType==1){
vm.doctor1 = data.data[i];
}else if(data.data[i].buildType==3){
vm.doctor2 = data.data[i];
}
}
vm.doctorNum = data.data.length;
}else{
tools.next('/m/familyOrg.html');
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});

},
methods:{
goToIndex(managerId,buildType){
tools.next(`/m/myFamilyDoctor.html?managerId=${managerId}&buildType=${buildType}`);
},
applyNewDoctor(){

tools.next('/m/familyOrg.html');

}
}
}



